<template>
  <div class="content">
    <van-nav-bar
      title="任务详情"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
    <div class="pay_way_group">
      <div class="pay_way_title">任务介绍</div>
      <van-cell-group class="payment_group">
        <van-cell title="任务编号">{{taskView.task.task_sn}}</van-cell>
        <van-cell title="责任人">{{taskView.task.superior_name}}</van-cell>
        <van-cell title="团队负责人">{{taskView.task.tuan_user_name}}</van-cell>
        <van-cell title="任务总金额">{{taskView.task.total_price}}</van-cell>
        <van-cell title="任务总佣金">{{taskView.task.gain_money_total}}</van-cell>
        <van-cell title="分配时间">{{taskView.task.create_time}}</van-cell>
        <van-cell title="任务状态">{{taskView.task.status_text}}</van-cell>
      </van-cell-group>
    </div>
    <div v-if="taskView.order.length > 0" class="pay_way_group" style=" padding-bottom: 10px;">
      <div class="pay_way_title">任务商品</div>
      <van-row class="upload_hint text-desc">
        <van-icon name="hint"/>
        点击查看详情，可提交任务商品实际下单价格等信息！
      </van-row>
      <van-card v-if="taskView.order"
        v-for="(order, orderI) in taskView.order"
        class="order_list--van-card"
        :key="orderI"
        :thumb="order.goods_pic">
        <div slot="title" class="van-card__row">
          <div class="van-card__title">{{ order.goods_name }}</div>
          <div class="van-card__price" style="color: #999;">
            金额：{{order.money}}
          </div>
        </div>
        <div slot="desc" class="van-card__row">
          <div class="van-card__desc">{{'店铺名：' + order.shop_name}}</div>
          <div class="van-card__num">{{'SKU：' + order.goods_sku}}</div>
        </div>
        <div slot="footer" class="order_list--footer_btn">
          <van-button size="small" type="danger" @click="toDetails(orderI)" >查看详情</van-button>
        </div>
      </van-card>
    </div>
    <!--任务图片上传 start -->
    <div class="id_card_upload">
      <div class="pay_way_title" style="padding:0;">任务图片上传</div>
      <van-row class="upload_hint text-desc" style="color: darkred">
        <van-icon name="hint"/>
        请点击下方图标上传图片，可多次点击上传，最多上传8张图片，默认第一张图为主图！
      </van-row>
      <div class="upload-img-5">
        <p style="font-size: 12px"></p>
        <div class="task-upload-list clearfix" v-if="picNum<=0 && taskStatus==1">
          <div class="u-item" v-for="(item,nn) in taskPic" :key="nn">
            <img :src="item.content" alt="图片" class="imgPreview">
            <van-icon name="close" @click="delImg(nn)" class="task-pic-del"/>
          </div>
          <van-uploader :after-read="onRead" :accept="'image/*'"  v-show="taskPic.length<=7">
            <img :src="uploadBtn" alt="等待传图" class="imgTipBtn">
          </van-uploader>
        </div>
        <div v-else class="task-upload-list clearfix">
          <div class="u-item" v-for="(item,nn) in picList" :key="nn">
            <img :src="item.pic" alt="图片" class="imgPreview">
          </div>
        </div>
      </div>
    </div>
    <!--任务图片上传 end -->
<!--    <uploadNew v-if="isUpload" @setFiles = "setFiles" ref="uploadNew"/>-->
    <van-button v-if="taskStatus==1" class="pay_submit" @click="subTask" :loading="isSubmit" type="danger" :bottom-action="true">提交审核
    </van-button>
    <van-button class="pay_submit" @click="goBack" type="default" :bottom-action="true" style="margin-top:10px;">返回上级
    </van-button>
  </div>
</template>

<script>
  import {
    Row,
    Col,
    CellGroup,
    Cell,
    Card,
    Button,
    Dialog,
    Uploader,
    ImagePreview,
    Toast,
    NavBar,
    Field
  } from "vant";
  import { TASK_VIEW,SUB_TASK} from "@/api/task";
  import uploadNew from "./upload-new";
  export default {
    name: "taskViews",
    data() {
      return {
        taskStatus:'',
        isSubmit : false,
        isUpload:true, //是否需要 上次身份图片
        uploadBtn: require(`@/assets/images/upload_btn.png`),
        taskPic:[],
        oFile: [],
        picNum:'0',
        picList:[],
        files:{
          taskFile:[],
        },
        taskView: {
          "task": {
            "id": "",
            "task_sn": "",
            "superior_name": "",
            "tuan_user_name": "",
            "city": "",
            "total_price": "",
            "gain_money_total": "",
            "create_time": "",
            "update_time": "",
          },
          "order": [
            {
              "goods_pic":"",
              "goods_name":"",
              "goods_num":"",
              "goods_sku":"",
              "keyword":"",
              "money":"",
              "shop_name":"",
            }
          ],
        },
        errorInfo: {
          shopName: false,
          others1:false,
          theShop: false,
          orderNo: false
        }
      };
    },
    props: {
      id: this.id
    },
    watch:{
			'$route': 'changeRouter'
		},
    created(){
      this.initPage();
    },

    methods: {
      initPage(){
        if(!this.id){
          return;
        }
        this.$reqGet(TASK_VIEW, {
          id: this.id,
        }).then((res) => {
          this.getSuccess(res);
        }).catch((err) => {
          this.getSuccess(err);
        });
      },
      getSuccess(res){
        let result = res.data;
        if(!result || result.code != 0){
          let msg = result.msg ? result.msg : "获取任务失败";
          Toast.fail(msg);
          return;
        }
        let data = result.data;
        this.taskView.task = data;
        this.taskStatus = data.status;
        this.taskView.order = data.order;
        this.picNum = data.pic_count;
        this.picList = data.task_pic;
      },
      toDetails(i){
        let id = this.taskView.order[i].id;
        this.$router.push({ name: "orderViews", params: { id: id} })
      },
      setFiles(file){
        this.files = file
      },
      onRead (files) { // 上传图片到图片服务器 //multiple
        this.taskPic.push(files)  // postData是一个数组
        let img = new Image();
        img.src = files.content;
        let that=this;
        img.onload=function(){
          let f1 = that.compress(img);
          that.oFile.push(f1.file)
          that.files.taskFile = that.oFile;
        }
      },
      delImg (index) {
        // 删除指定下标的图片对象
        if (isNaN(index) || index >= this.taskPic.length) {
          return false
        }
        let tmp = []
        let otmp = [];
        for (let i = 0, len = this.taskPic.length; i < len; i++) {
          if (this.taskPic[i] !== this.taskPic[index]) {
            tmp.push(this.taskPic[i])
            otmp.push(this.oFile[i])
          }
        }
        this.taskPic = tmp
        this.oFile = otmp
        this.files.taskFile = this.oFile;
      },
      // 压缩图片
      compress(img){
        let originWidth = img.width, // 压缩后的宽
          originHeight=img.height,
          maxWidth = 600, maxHeight = 400,
          quality = 0.8, // 压缩质量
          canvas = document.createElement('canvas'),
          drawer = canvas.getContext("2d");
        canvas.width = maxWidth;
        canvas.height = originHeight/originWidth*maxWidth;
        drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
        let base64 = canvas.toDataURL("image/jpeg", quality); // 压缩后的base64图片
        let file = this.dataURLtoFile(base64,Date.parse(Date())+'.jpg');
        file={content:base64,file:file};
        return file;
      },
      // base64转file
      dataURLtoFile(dataurl,filename){
        let arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while(n--){
          u8arr[n] = bstr.charCodeAt(n);
        }
        return new File([u8arr],filename,{type:mime});
      },
      // 图片超出限制后 进行压缩
      maxReadBack(files){
        Toast('图片不能超出5M')
      },
      //提交审核
      subTask(){
        try {
          let _this = this;
          let formdata = new FormData();
          if(this.isUpload){
            let imgFile = this.files;
            if(imgFile=='' || typeof(imgFile)=='undefined'){
              Toast('请上传任务图片');
              return;
            }
            let images = this.files.taskFile;
            let imgNum = 0;
            images.forEach((img,index) => {
              formdata.append('taskFile_'+index,img)
              imgNum +=1;
            })
            formdata.append("fileNum", imgNum);
          }
          formdata.append("id", this.id)
          this.isSubmit = true
          this.$reqPost(SUB_TASK, formdata).then(res => {
            this.submitSuccess(res);
          }).catch(err => {
            this.isSubmit = false
          });
        } catch (e) {
          Toast(e.message)
        }
      },
      submitSuccess(res) {
        let result = res.data;
        if(result.code!=0){
          this.isSubmit = false;
          Toast.fail(result.msg);
          return;
        }
        Toast.success('提交成功');
        setTimeout(() => {
          this.$router.push({ name: "index"});
        }, 1000)

      },
      //返回
      goBack() {
        this.$router.go(-1)
      },

    },

    components: {
      [Row.name]: Row,
      [Col.name]: Col,
      [Card.name]: Card,
      [CellGroup.name]: CellGroup,
      [Cell.name]: Cell,
      [Button.name]: Button,
      [Uploader.name]: Uploader,
      [ImagePreview.name]: ImagePreview,
      [Toast.name]: Toast,
      [NavBar.name]:NavBar,
      [Field.name]:Field
    },

    filters: {
      money: function(val) {
        val = val.toString().replace(/\$|\,/g,'');
        if(isNaN(val)) {
          val = "0";
        }
        let sign = (val == (val = Math.abs(val)));
        val = Math.floor(val*100+0.50000000001);
        let cents = val%100;
        val = Math.floor(val/100).toString();
        if(cents<10) {
          cents = "0" + cents
        }
        for (var i = 0; i < Math.floor((val.length-(1+i))/3); i++) {
          val = val.substring(0,val.length-(4*i+3))+',' + val.substring(val.length-(4*i+3));
        }
        return (((sign)?'':'-') + val + '.' + cents);
      }
    }
  };

</script>

<style lang="scss" scoped>

  .id_card_upload {
    margin: 5px 0 5px 0;
    background-color: #fff;
    padding: 15px;
    > div {
      margin-bottom: 15px;
    }
  }
  .task-upload-list {
    padding:10px 0;
  }
  .task-upload-list .u-item{
    float:left;
    width:23%;
    height:100px;
    padding:1%;
    position:relative;
    marbin-bottom:20px;
  }
  .task-upload-list .imgPreview {
    width:100%;
    height:100px;
  }.task-upload-list .imgTipBtn {
     width:80px;
     height:80px;
   }
  .task-upload-list .task-pic-del {
    position:absolute;
    top:8px;
    right:8px;
    font-size:20px;
    color:#f00;
    z-index:11;
  }
  .task-countdown {
    text-align: center;
    font-size: 12px;
    background-color: #fffeec;
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    z-index: 999;
  }

  .pay_submit {
    /*position: fixed;*/
    /*bottom: 0;*/
    width: 100%;
  }

  .cannel_submit{
    margin-top:10px;
    width: 100%;
  }

  .pay_way_group{
    background-color: #ffffff;
    margin-bottom: 10px;
  }

  .pay_way_group img {
    vertical-align: middle;
  }

  .pay_way_title {
    font-weight: bold;
    padding: 15px;
    background-color: #fff;
  }

  .upload_hint{
    position: relative;
    padding-left: 20px;
    line-height: 1.6;
    margin: 10px;
    i{
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  .vacancy_row{
    .van-col {
      font-size: 13px;
      line-height: 30px;
      text-align: center;
      vertical-align: middle;
      margin-bottom: 0px;
      background-clip: content-box;
    }
  }

  .countdown{
    text-align: center;
    width: 100%;
    height: 50px;
    line-height: 50px;
    border: 0;
    border-radius: 0;
    font-size: 16px;
    color: #fff;
    background-color: #29a0f6;
  }

  .van-cell__right-icon {
    top: 3px;
  }

  .text_style {
    color: #29a0f6;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .text_red{
    color:red;
    font-size: 12px;
    padding-left: 15px;
    text-align: left;
    margin: 0;
    padding-bottom: 7px;
  }
  .no-longtap{
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }
</style>



